import React from 'react';

const CardStyle = {
  backgroundColor: '#ffffff',
  color: '#000000',
  position: 'relative',
  paddingBottom: '5px',
  textAlign: 'center',
  border: '1px solid #dedede',
  borderRadius: '3px',
  marginTop: '40px',
};

class Card extends React.Component {
  render() {
    return (
      <div style={CardStyle}>
        {this.props.children}
      </div>
    );
  }
}

export default Card;


