import React, { Component } from 'react';
import styled from 'styled-components';
import { Grid, Row, Col } from 'react-bootstrap';
import Card from './components/Card.js';
import ProfileCard from './components/ProfileCard.js';
import ContentCard from './components/ContentCard.js';
import MediaFrame from './components/MediaFrame.js';
import VideoFrame from './components/VideoFrame.js';
import HeaderContent from './components/HeaderContent.js';
import ListContent from './components/ListContent.js';


const photography = require.context('./img/photography', true);
const pictures = ['./pic1.png', './pic2.png', './pic3.png', './pic4.png', './pic5.png', './pic6.png', './pic0.png'].map(name => photography(name, true));
const picturesDesc = "All the photos below were taken the summer of 2017. My family and I travelled to Seattle and Banff / Jasper National Park. The year I went was very special because it was Canada's 150 year anniversary of Parks Canada.";
const projects = require.context('./img/projects', true);
const projectPictures = ['./encryption.png', './wpm.png'].map(name => projects(name, true));
const projectLinks = ['https://talonjin.github.io/encrypt/', 'https://talonjin.github.io/wpm/'];
const projectPicturesDesc = "Here are some highlights of the projects that I've worked on. Click on the image see the project!";
const profile = require.context('./img/profile', true);


var schoolClubs = ['Computer Club','Math Club','Swimming Team','Badminton Team','Tennis Team','Table Tennis Team'];
var gr12Subjects = ['MHF4UE - Advanced Functions AP','ICS4U - Computer Science','ENG4U - English','SPH4U - Physics'];
var gr11Subjects = ['ICS3U - Computer Science','ENG3UE - English AP','SPH3UE - Physics AP','BAF3M - Accounting','MCR3UE - Math AP','SCH3U - Chemistry'];
var awards = ['Top 10% Senior Canadian Computing Competition','Honor Roll (above 90% avg)','Chemistry Award (highest grade)','Physics Award (highest grade)','RCM Level 8 & Advanced Theory','50m Fly club record'];
var interests = ['🎾 Tennis','🏓 Table Tennis','🏸 Badminton','🏊‍♂️ Competitive Swimming','🏀 Basketball','🎲 Board Games (Catan)','🎹 Piano','🏒 Hockey'];
var swimmingInstructor = ['Taught many children of various levels including impaired swimmers the fundamental techniques of swimming and floating','Integrated water safety lessons and games','Communicated with parents/guardians regarding the swimmers strengths and areas of improvement'];
var uoftcamp = ['Learned how to program autonomous vehicles in Python with LEGO Mindstorms','Implemented Google’s Material UI components to web pages using React','Programmed games using p5.js'];
var digitalMediaAcademy = ['Programmed League of Legends mini arcade game with Pygame', 'Integrated user input for keyboard and mouse'];
var vaughanAquaticClub = ['Train seven times a week (~20 hours / week)','Competed at high level swim meets including Provincials and OFSAA','Received the 50 meter Butterfly club record'];
var swimCoachAssistant = ['Coached the high school swim team every practice','Created new swim workouts every practice to cater towards different level swimmers','Demonstrated proper swimming technique and drills for efficient movement under water'];

const videoDesc = "Here are some videos of me playing my favorite piano songs.";
const videosource = require.context('./videos', true);
const videos = ['./battle_symphony.mp4', './hallelujah.mp4'].map(name => videosource(name, true));
const videoTitles = ['Linkin Park - Battle Symphony', 'Leonard Cohen - Hallelujah'];

const Background = styled.div`
  background-color: #eff5ff;
  padding: 1em 1em 4em 1em;
`;

class App extends Component {
  render() {
    return (
      <Background>
        <Grid>
          <Row>
            <Col xs={12} md={4}>
              <Card>
                <ProfileCard profile={profile('./MainFace.png')} cover={profile('./MainCover.png')}/>
              </Card>

              <Card>
                <ContentCard title='💻 Skills'>
                  <p>Python, Java, JavaScript, Git, React, JSX, SASS, HTML, CSS</p>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='🏆 Awards'>
                  <ListContent primary items={awards}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='🎓 Education'>
                  <p>Vaughan Secondary School</p>
                  <p>Sep 2014 - Jun 2018</p>
                  <ListContent items={schoolClubs}/>

                  <p>Gr12 (95.25% average):</p>
                  <ListContent items={gr12Subjects}/>

                  <p>Gr11 (94.70% average):</p>
                  <ListContent items={gr11Subjects}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='Volunteering'>
                  <HeaderContent title='Emily Carr S.S.' date='Sep 2014 - Feb 2016' role='Swim Coach Assistant'/>
                  <ListContent primary items={swimCoachAssistant}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='Interests'>
                  <ListContent items={interests}/>
                </ContentCard>
              </Card>
            </Col>

            <Col xs={12} md={8}>
              <Card>
                <ContentCard title='My Videos'>
                  <VideoFrame desc={videoDesc} videos={videos} videoTitles={videoTitles}/>
                </ContentCard>
              </Card>
              <Card>
                <ContentCard title='Projects'>
                  <MediaFrame pictures={projectPictures} desc={projectPicturesDesc} links={projectLinks}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='📷 Photography'>
                  <MediaFrame pictures={pictures} desc={picturesDesc}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='Extracurriculars'>
                  <HeaderContent title='Vaughan Aquatic Club' date='Sep 2009 - Present' role='Competitive Swimmer'/>
                  <ListContent primary items={vaughanAquaticClub}/>

                  <HeaderContent title='University of Toronto DEEP Camp' date='Aug 2015 - Aug 2017' role='React Web Dev & Vehicle Dev'/>
                  <ListContent primary items={uoftcamp}/>

                  <HeaderContent title='Digital Media Academy' date='Aug 2016 - Sep 2016' role='Game development with Python'/>
                  <ListContent primary items={digitalMediaAcademy}/>
                </ContentCard>
              </Card>

              <Card>
                <ContentCard title='💼 Work Experience'>
                  <HeaderContent title='Chancellor Community Centre' date='Jan 2017 - Apr 2017' role='Swimming Instructor'/>
                  <ListContent primary items={swimmingInstructor}/>
                </ContentCard>
              </Card>

            </Col>
          </Row>
        </Grid>
      </Background>
    );
  }
}

export default App;



