import React from 'react';
import styled from 'styled-components';

const MainFace = styled.img`
  border: 5px solid #ffffff;
  border-radius: 50%;
  width: 56%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  z-index: 1;
  position: relative;
`;

const MainCover = styled.img`
  width: 100%;
  display: block;
  position: absolute;
`;

const TopPad = styled.div`
  padding-top: 20%;
`;

const SidePad = styled.div`
  padding-left: 10%;
  padding-right: 10%;
`;

const Footer = styled.div`
  margin-bottom: 10px;

  a {
    margin: 0em 5px;
    font-size: 1.5em;
  }
`;

class ProfileCard extends React.Component {
  render() {
    return (
      <div>
        <MainCover src={this.props.cover}></MainCover>
        <TopPad>
          <MainFace src={this.props.profile}></MainFace>
        </TopPad>
        <SidePad>
          <h1> Talon Jin </h1>
          <p> 👋, I'm a grade 12 student that enjoys competitive swimming, travelling, and coding. Seeking to dive deeper into the field of computer science and software engineering. </p>
        </SidePad>
        <Footer>
          <a target='_blank' href='https://www.instagram.com/talon_jin/'><i class="fab fa-instagram"></i></a>
          <a target='_blank' href={require('../files/talon_resume.pdf')}><i className="fas fa-file"></i></a>
          <a target='_blank' href='https://github.com/talonjin'><i className="fab fa-github"></i></a>
          <a target='_blank' href='https://www.linkedin.com/in/talonjin'><i className="fab fa-linkedin"></i></a>
          <a target='_top' href='mailto:talonjin2000@gmail.com?Subject=Hello%20Talon'><i className="fas fa-envelope"></i></a>
        </Footer>
      </div>
    );
  }
}

export default ProfileCard;


