import React from 'react';

const ContentCardStyle = {
  paddingLeft: '10%',
  paddingRight: '10%',
  textAlign: 'left',
};

class ContentCard extends React.Component {
  render() {
    return (
      <div style={ContentCardStyle}>
        <h1> {this.props.title} </h1>
        <div>
          {this.props.children}
        </div>
      </div>
    );
  }
}

export default ContentCard;


