import React from 'react';
import styled from 'styled-components';
import ReactPlayer from 'react-player';

const Button = styled.button`
  background-color: #ffffff;
  font-size: 2em;
  margin: 0.5em 0.25em;
  padding: 0em 0.5em;
  border: 1px solid #c7c7c7;
  border-radius: 3px;

  &:hover {
    background-color: #c7c7c7c7;
  }

  &:focus {
      outline:none;
  }
`;

class VideoFrame extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      video:null
    };
    this.changeVideo = this.changeVideo.bind(this);
  }

  componentDidMount() {
    var width = this.refs.frame.parentNode.clientWidth;
    var height = this.refs.frame.parentNode.clientWidth * 9 / 16;
    console.log(this.props.videos);
    this.videos = this.props.videos.map(video => <ReactPlayer url={video} playing={true} width={width+'px'} height={height+'px'} onEnded={this.changeVideo.bind(this, 1)} controls/>);
    this.idx = 0;
    this.setState({
      video: this.videos[this.idx]
    });
  }

  changeVideo(direction) {
    if (direction > 0) {
      if (this.idx + 1 >= this.videos.length) {
        this.idx = 0;
      } else {
        this.idx += 1;
      }
    } else {
      if (this.idx < 0) {
        this.idx = this.videos.length - 1;
      } else {
        this.idx -= 1;
      }
    }
    
    this.setState({video: this.videos[this.idx]});
  }

  render() {
    return (
      <div ref='frame'>
        <p>{this.props.desc}</p>
        <h3>{this.props.videoTitles[this.idx]}</h3>
        {this.state.video}
        <div style={{textAlign: 'center'}}>
          <Button onClick={this.changeVideo.bind(this, -1)}> 👈 </Button>
          <Button onClick={this.changeVideo.bind(this, 1)}> 👉 </Button>
        </div>
      </div>
    );
  }
}

export default VideoFrame;


